import uniNavBar from "@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue"
import price from "@/components/common/price.vue"
import uniNumberBox from "@/components/uni-ui/uni-number-box/uni-number-box.vue"
//引入弹出框组件
import commonPopup from "@/components/common/common-popup.vue"
// 引入卡片组件
import card from "@/components/common/card.vue"
// 引入多选组件
import zcmRadioGroup from "@/components/common/radio-group.vue"
import {mapState,mapGetters,mapActions,mapMutations} from "vuex"
export default {
	components: {
		uniNavBar,
		price,
		uniNumberBox,
		card,
		zcmRadioGroup,
		commonPopup
	},
	data() {
		return {
			isedit: false,	
			baseUrl: ''
		}
	},
	computed: {
		...mapState({
			list:state=>state.cart.list,
			popupShow:state=>state.cart.popupShow,
			changenumBtn:state=>state.cart.changenumBtn,
		}),
		...mapGetters([
			'checkedAll',
			'totalPrice',
			'disableSelectAll',
			'popupData'
		])
	},
	methods:{
		...mapActions([
			'doSelectAll',
			'doDelGoods',
			'doShowPopup',
			'doHidePopup',
			'updateCartList',
			'addCartList'
		]),
		...mapMutations([
			'selectItem',
			'initCartList',
		]),
		onLoad(options) {
			//设置基础的url
			this.baseUrl = this.$H.common.ul
			// 获取购物车数据
			this.updateCartList()
		},
		changeNum(e,item,index){
			//先改变本商品的数量值
			let list = this.list;
			list[index].num = e
		    this.list = list
			// //然后往后台发信息
			this.addCartList({
				'index':index,
				'num':e,
				'hashKey':item.hashKey 
			})
		},
		clickBtn(){
			//按钮
			this.changenumBtn = !this.changenumBtn
		},
		/**
		 * 提交订单
		 */
		submitOrder()
		{
			//1 购物车订单数据入缓存
			this.$Util.setCache("current_order_info",this.list,0);
			//2 跳转到订单结算页面 ordersubmit
			uni.navigateTo({
				url:"../../pages/mannerorder/ordersubmit"
			})
		}
	}
}
